<template>
    <textarea :id="id">
    </textarea>
</template>

<script>

    import Vue from 'vue'
    export default {
        name: "tinymce",
        props: ['id', 'options', 'content'],
        mounted () {

            //Get default options
            let options = {};
            if(this.options == 'object'){
                
                options = this.options
                if(!options.hasOwnProperty('selector')){
                    options.selector = '#' + this.id
                }

            } else options.selector = '#' + this.id

            options.setup = (editor) => {
                editor.on('NodeChange Change KeyUp', (e) => {
                    this.$emit('change', tinymce.get(this.id), tinymce.get(this.id).getContent())
                })
                editor.on('init', (e) => {
                    if(this.content != undefined){
                        tinymce.get(this.id).setContent(this.content)
                    }
                })
            }

            //Initialize tinymce
            Vue.nextTick(() => {
			    tinymce.init(options)
		    })
        },
        beforeDestroy () {

            //Delete instance of tinymce
            tinymce.execCommand('mceRemoveEditor', false, this.id)
        }
    }
</script>


